 
export const dateConverter = (date: string): string => {

    let day = (+   date.split('-')[2].split('T')[0]        )      %100;

    let month = date.split('-')[1];
    switch (month) {
        case '01': month = ''; break;
        case '02': month = ''; break;
        case '03': month = ''; break;
        case '04': month = ''; break;
        case '05': month = ''; break;
        case '06': month = ''; break;
        case '07': month = ''; break;
        case '08': month = ''; break;
        case '09': month = ''; break;
        case '10': month = ''; break;
        case '11': month = ''; break;
        case '12': month = ''; break;
        default: break;
    };
    let year = date.split('-')[0];

    let newDate = day + ' ' + month + ' ' + year;
    return newDate;
}
  
